import React, { useState, useEffect } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, A11y } from "swiper/modules";
import { collection, getDocs } from "firebase/firestore";
import { db } from "../firebase";

import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";

export default function EditionSlider() {
  const [editionData, setEditionData] = useState([]);
  const [alertMsg, setAlertMsg] = useState(null);

  useEffect(() => {
    const fetchEditions = async () => {
      try {
        const colRef = collection(db, "editions");
        const snapshot = await getDocs(colRef);
        const data = snapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));
        setEditionData(data);
      } catch (error) {
        console.error("Failed to fetch editions:", error);
      }
    };
    fetchEditions();
  }, []);

  const openPDF = (url) => {
    if (!url) {
      setAlertMsg("No PDF available for this edition.");
      return;
    }
    const confirmed = window.confirm("Do you want to open this edition?");
    if (confirmed) {
      window.open(url, "_blank");
    }
  };

  const closeAlert = () => {
    setAlertMsg(null);
  };

  return (
    <>
      <div className="w-full max-w-7xl mx-auto px-4 py-10">
        <h1 className="text-3xl font-bold text-center mb-8 text-gray-800">
          The Guide Editions
        </h1>

        <Swiper
          modules={[Navigation, Pagination, A11y]}
          navigation
          pagination={{ clickable: true }}
          spaceBetween={25}
          breakpoints={{
            0: { slidesPerView: 1 },
            640: { slidesPerView: 1 },
            768: { slidesPerView: 2 },
            1024: { slidesPerView: 3 },
          }}
          className="rounded-2xl p-6"
        >
          {editionData.map((slide) => (
            <SwiperSlide key={slide.id}>
              <div className="flex flex-col h-[560px] rounded-xl overflow-hidden ">
                <div className="w-full h-[420px]">
                  <img
                    src={slide.imageURL}
                    alt={slide.name}
                    className="w-full h-full object-cover"
                  />
                </div>

                <div className="p-4 flex flex-col items-center justify-between flex-1">
                  <h2 className="text-xl font-bold text-gray-800">
                    {slide.name}
                  </h2>

                  <button
                    onClick={() => openPDF(slide.pdfURL)}
                    className="mt-3 text-white font-semibold py-2 px-6  shadow-md bg-[#9b0c0c] transition"
                  >
                    Read Issue
                  </button>
                </div>
              </div>
            </SwiperSlide>
          ))}
        </Swiper>
      </div>

      {/* Centered Alert / Modal */}
      {alertMsg && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50">
          <div className="bg-white rounded-lg shadow-lg p-6 max-w-sm w-full mx-4">
            <p className="text-gray-800 mb-4">{alertMsg}</p>
            <button
              onClick={closeAlert}
              className="mt-2 bg-red-600 text-white py-2 px-4 rounded hover:bg-red-700 transition"
            >
              Close
            </button>
          </div>
        </div>
      )}
    </>
  );
}
