import { useParams, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { db } from "../firebase";
import { collection, getDocs } from "firebase/firestore";
import { ArrowLeft } from "lucide-react";

export default function Individual() {
  const { id } = useParams();
  const [article, setArticle] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchArticle = async () => {
      const snap = await getDocs(collection(db, "blogs"));
      const allBlogs = snap.docs.map((doc) => ({ ...doc.data(), docId: doc.id }));
      const found = allBlogs.find((blog) => String(blog.id) === String(id));
      setArticle(found || null);
    };

    fetchArticle();
  }, [id]);

  if (!article) {
    return (
      <div className="flex items-center justify-center h-screen bg-gradient-to-r from-[#9B2A22] to-[#2F0D0A]">
        <p className="text-white text-xl animate-pulse">Loading article...</p>
      </div>
    );
  }

  return (
    <div className="min-h-screen w-full bg-gradient-to-r from-[#9B2A22] to-[#2F0D0A] px-4 py-10 relative">

      {/* Back Button */}
      <button
        
        className="fixed top-6 right-6 bg-black/40 hover:bg-black/60 text-white p-3 rounded-full backdrop-blur-md transition"
      >
        <ArrowLeft size={26} />
      </button>

      <div className="max-w-4xl mx-auto bg-black/20 backdrop-blur-xl text-white p-10 rounded-2xl shadow-xl border border-white/10">

        {/* Article Title */}
        <h1 className="text-3xl md:text-5xl font-extrabold leading-tight mb-3">
          {article.title}
        </h1>

        {/* Author + Date */}
        <p className="text-gray-200 text-lg mb-8">
          By <span className="font-semibold">{article.author}</span>
          {article.date && (
            <span className="ml-2 opacity-70 text-sm"> {article.date}</span>
          )}
        </p>

   

        {/* Article Body */}
        <div className="prose prose-lg max-w-none text-gray-100 whitespace-pre-line leading-relaxed">
          {article.full}
          <button  onClick={() => navigate(-1)}
          className="bg-white text-black py-1 px-2 rounded-lg flex m-auto ">Back</button>
        </div>
      </div>
    </div>
  );
}
