import { useState, useEffect } from "react";
import { ArrowRight } from "lucide-react";
import { Link } from "react-router-dom";
import { db } from "../firebase";
import { collection, getDocs, query, orderBy } from "firebase/firestore";

export default function Slider() {
  const [articles, setArticles] = useState([]);
  const [current, setCurrent] = useState(0);

  // Fetch articles
  useEffect(() => {
    const fetchArticles = async () => {
      const q = query(collection(db, "blogs"), orderBy("createdAt", "desc"));
      const snap = await getDocs(q);
      const data = snap.docs
        .map((doc) => ({ id: doc.id, ...doc.data() }))
        .filter((a) => a.type?.toLowerCase() === "article")
        .slice(0, 5);

      setArticles(data);
    };

    fetchArticles();
  }, []);

  const nextSlide = () =>
    setCurrent((prev) => (prev === articles.length - 1 ? 0 : prev + 1));

  // Auto slide
  useEffect(() => {
    const interval = setInterval(nextSlide, 6000);
    return () => clearInterval(interval);
  }, [articles.length]);

  return (
    <div className="relative w-full overflow-hidden pb-5 bg-white min-h-[800px] sm:min-h-[800px] md:min-h-[600px] xs:min-h-[1200px] ">
      {articles.map((slide, index) => (
        <div
          key={slide.id}
          className={`absolute inset-0 flex flex-col md:flex-row
            items-start md:items-center justify-start 
            w-full max-w-[1300px] mx-auto px-4 
            gap-6 md:gap-10 bg-gray-200/40 rounded-2xl p-6 
            transition-opacity duration-1000 
            md:min-h-[500px] 
            ${
              index === current
                ? "opacity-100 z-10"
                : "opacity-0 pointer-events-none z-0"
            }`}
        >
          {/* IMAGE */}
          <div className="w-full md:w-1/2 flex justify-center md:items-center flex-shrink-0">
            <div className="w-full max-w-[520px] h-[260px] sm:h-[300px] md:h-[420px]">
              <img
                src={slide.image}
                alt={slide.title}
                className="w-full h-full object-cover rounded-2xl shadow-xl"
              />
            </div>
          </div>

          {/* MOBILE BUTTONS */}
          <div className="flex gap-3 w-full justify-center md:hidden mt-4 mb-3 z-50">
            <Link
              to={`/individual/${slide.id}`}
              className="bg-[#2b0d0d] text-white px-6 py-2 rounded-full hover:bg-[#451515] transition shadow-md"
            >
              Read
            </Link>
            <button
              onClick={nextSlide}
              className="bg-[#2b0d0d] p-3 rounded-full hover:bg-[#451515] transition shadow-md"
            >
              <ArrowRight className="text-white w-5 h-5" />
            </button>
          </div>

          {/* TEXT CONTAINER */}
          <div className="w-full md:w-1/2 flex flex-col p-4 rounded-xl md:h-[420px]">
            <h1 className="text-2xl md:text-3xl font-semibold text-[#2b0d0d] mb-2">
              {slide.title}
            </h1>

            <p className="text-gray-700 text-lg md:text-xl font-medium mb-2">
              {slide.author || "Unknown"}
            </p>

            <p className="text-gray-700 text-lg leading-relaxed">
              {slide.short}
            </p>
          </div>

          {/* DESKTOP BUTTONS */}
          <div className="hidden md:flex gap-3 absolute bottom-6 left-1/2 -translate-x-1/2 md:ml-5">
            <Link
              to={`/individual/${slide.id}`}
              className="bg-[#2b0d0d] text-white px-6 py-2 rounded-full hover:bg-[#451515] transition shadow-md"
            >
              Read
            </Link>
            <button
              onClick={nextSlide}
              className="bg-[#2b0d0d] p-3 rounded-full hover:bg-[#451515] transition shadow-md"
            >
              <ArrowRight className="text-white w-5 h-5" />
            </button>
          </div>
        </div>
      ))}
    </div>
  );
}
