import React, { useState } from "react";
import { db } from "./firebase";
import { collection, addDoc, serverTimestamp } from "firebase/firestore";
import { useNavigate } from "react-router-dom";
import { X } from "lucide-react";

export default function UploadBlog() {
  const navigate = useNavigate();

  const [data, setData] = useState({
    id: "",
    title: "",
    author: "",
    full: "",
    short: "",
    type: "",
    date: "",
  });

  const [image, setImage] = useState(null);
  const [imageURL, setImageURL] = useState("");

  // Upload ONLY image to Cloudinary
  const uploadImage = async () => {
    if (!image) return "";

    const formData = new FormData();
    formData.append("file", image);
    formData.append("upload_preset", "the guide");
    formData.append("cloud_name", "dwzultetk");

    const res = await fetch(
      "https://api.cloudinary.com/v1_1/dwzultetk/image/upload",
      {
        method: "POST",
        body: formData,
      }
    );

    const uploaded = await res.json();
    setImageURL(uploaded.secure_url);
    return uploaded.secure_url;
  };

  const handleSubmit = async () => {
    const imgUrl = await uploadImage();

    const blogData = {
      ...data,
      image: imgUrl,
      createdAt: serverTimestamp(),
    };

    await addDoc(collection(db, "blogs"), blogData);

    alert("Blog uploaded successfully!");
    setData({
      id: "",
      title: "",
      author: "",
      full: "",
      short: "",
      type: "",
      date: "",
    });
    setImage(null);
    setImageURL("");
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-100 px-4 relative">

      {/*  ABSOLUTE TOP-RIGHT BACK BUTTON */}
      <button
        onClick={() => navigate(-1)}
        className="absolute top-4 right-4 p-2 bg-red-500 text-white rounded-full shadow hover:bg-red-600 transition"
      >
        <X size={20} />
      </button>

      <div className="w-full max-w-xl bg-white p-8 rounded-2xl shadow-lg mt-10 relative">

        <h2 className="text-2xl font-semibold mb-6 text-center text-gray-800">
          Upload Blog
        </h2>

        <div className="space-y-4">

          <input
            type="text"
            placeholder="Blog ID"
            className="w-full p-3 border rounded-lg"
            value={data.id}
            onChange={(e) => setData({ ...data, id: e.target.value })}
          />

          <input
            type="text"
            placeholder="Title"
            className="w-full p-3 border rounded-lg"
            value={data.title}
            onChange={(e) => setData({ ...data, title: e.target.value })}
          />

          <input
            type="text"
            placeholder="Author"
            className="w-full p-3 border rounded-lg"
            value={data.author}
            onChange={(e) => setData({ ...data, author: e.target.value })}
          />

          <input
            type="text"
            placeholder="Short Description"
            className="w-full p-3 border rounded-lg"
            value={data.short}
            onChange={(e) => setData({ ...data, short: e.target.value })}
          />

          <input
            type="text"
            placeholder="Type"
            className="w-full p-3 border rounded-lg"
            value={data.type}
            onChange={(e) => setData({ ...data, type: e.target.value })}
          />

          <input
            type="text"
            placeholder="Date"
            className="w-full p-3 border rounded-lg"
            value={data.date}
            onChange={(e) => setData({ ...data, date: e.target.value })}
          />

          <textarea
            rows="5"
            placeholder="Full Content"
            className="w-full p-3 border rounded-lg"
            value={data.full}
            onChange={(e) => setData({ ...data, full: e.target.value })}
          ></textarea>

          <div>
            <label className="block font-medium mb-1">Upload Image</label>
            <input
              type="file"
              className="w-full p-2 border rounded-lg"
              onChange={(e) => setImage(e.target.files[0])}
            />
          </div>
        </div>

        <button
          onClick={handleSubmit}
          className="w-full mt-6 bg-blue-600 hover:bg-blue-700 text-white py-3 rounded-xl font-medium"
        >
          Submit
        </button>

        {imageURL && (
          <div className="mt-6">
            <h3 className="font-medium text-gray-700 mb-2">
              Uploaded Image Preview
            </h3>
            <img
              src={imageURL}
              alt="Uploaded"
              className="rounded-xl shadow-md"
            />
          </div>
        )}
      </div>
    </div>
  );
}
