import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Papa from "papaparse";

export default function MixedAll() {
  const [techData, setTechData] = useState([]);

  useEffect(() => {
    const sheetUrl =
      "https://docs.google.com/spreadsheets/d/e/2PACX-1vQeomN__blTC2EylL4uEwGxLXMh0zxQ0F5BlWwBNHySrtJwqw9aJe9Zw0166-pDhaboaM4GdTlhzAET/pub?gid=0&single=true&output=csv";

    Papa.parse(sheetUrl, {
      download: true,
      header: true,
      complete: (result) => {
        const cleaned = result.data.filter(
          (row) => row.id && row.title && row.image
        );

        //  Sort by latest ID
        cleaned.sort((a, b) => Number(b.id) - Number(a.id));

        setTechData(cleaned);
      },
    });
  }, []);

  const allArticles = [...techData]; // show ALL articles

  return (
    <div className="min-h-screen flex flex-col items-center pt-20 bg-gradient-to-r from-[#9B2A22] to-[#2F0D0A] w-full px-6 pb-10">
      <div className="w-full max-w-6xl text-left">
        <h1 className="text-4xl sm:text-5xl font-bold text-white mb-10 pb-2">
          All Articles
        </h1>
      </div>

      <div className="grid grid-cols-1 sm:grid-cols-3 lg:grid-cols-4 gap-[40px] max-w-[1500px] w-full">
        {allArticles.map((article, index) => (
          <Link
            key={index}
            to={`/individual/${article.id}`}
            state={{ article }}
            className="group block bg-[#EBEAEA] rounded-2xl shadow-md overflow-hidden relative hover:shadow-xl transition-shadow duration-300"
          >
            <div className="relative p-3 overflow-hidden rounded-2xl">
              <div className="relative bg-white rounded-2xl overflow-hidden">
                <img
                  src={article.image}
                  alt={article.title}
                  className="w-full h-60 object-cover transform transition-transform duration-700 group-hover:scale-105 rounded-2xl"
                />
              </div>

              <span className="absolute top-3 left-3 text-white text-xs px-3 py-1 bg-black/40 rounded">
                {article.category}
              </span>

              <h3 className="absolute top-10 left-6 w-[180px] text-md font-semibold text-white text-left">
                {article.title}
              </h3>
            </div>

            <div className="p-4">
              <div className="mt-3 text-sm text-gray-600">
                <p className="font-semibold text-xl text-[#8B0D0D]">
                  {article.author}
                </p>
                <p>{article.time}</p>
              </div>
            </div>
          </Link>
        ))}
      </div>
    </div>
  );
}
