// src/MyspaceForm.js
import React, { useState } from "react";
import { db } from "./firebase";
import { collection, addDoc } from "firebase/firestore";
import { useNavigate } from "react-router-dom";
import { X } from "lucide-react";

export default function MyspaceForm() {
  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    id: "",
    title: "",
    author: "",
    college: "",
    full: "",
    type: "",
    time: "",
  });

  const [message, setMessage] = useState("");

  // Handle input change
  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  // Submit form
  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const collectionRef = collection(db, "myspaceData");
      await addDoc(collectionRef, formData);

      setMessage("Data submitted successfully!");

      // Reset form
      setFormData({
        id: "",
        title: "",
        author: "",
        college: "",
        full: "",
        type: "",
        time: "",
      });
    } catch (error) {
      console.error("Error adding document: ", error);
      setMessage("Error submitting data.");
    }
  };

  return (
    <div className="relative max-w-md mx-auto p-4 bg-white shadow-lg rounded-xl mt-10">

      {/*  TOP-RIGHT CLOSE BUTTON */}
      <button
        onClick={() => navigate(-1)}
        className="absolute top-3 right-3 p-2 bg-red-500 text-white rounded-full shadow hover:bg-red-600 transition"
      >
        <X size={18} />
      </button>

      <h2 className="text-xl font-bold mb-4 text-gray-800">Myspace Form</h2>

      {message && <p className="mb-4 text-green-600">{message}</p>}

      <form onSubmit={handleSubmit} className="space-y-3">

        <input
          type="text"
          name="id"
          placeholder="ID"
          value={formData.id}
          onChange={handleChange}
          className="w-full p-2 border rounded"
          required
        />

        <input
          type="text"
          name="title"
          placeholder="Title"
          value={formData.title}
          onChange={handleChange}
          className="w-full p-2 border rounded"
          required
        />

        <input
          type="text"
          name="author"
          placeholder="Author"
          value={formData.author}
          onChange={handleChange}
          className="w-full p-2 border rounded"
          required
        />

        <input
          type="text"
          name="college"
          placeholder="College"
          value={formData.college}
          onChange={handleChange}
          className="w-full p-2 border rounded"
        />

        <textarea
          name="full"
          placeholder="Full Content"
          value={formData.full}
          onChange={handleChange}
          className="w-full p-2 border rounded"
          rows="4"
          required
        />

        {/* Select Dropdown */}
        <select
          name="type"
          value={formData.type}
          onChange={handleChange}
          className="w-full p-2 border rounded"
          required
        >
          <option value="">Select Type</option>
          <option value="Poem">Poem</option>
          <option value="Story">Story</option>
          <option value="Article">Article</option>
          <option value="Others">Others</option>
        </select>

        <input
          type="text"
          name="time"
          placeholder="Time"
          value={formData.time}
          onChange={handleChange}
          className="w-full p-2 border rounded"
        />

        <button
          type="submit"
          className="w-full bg-blue-600 text-white py-2 px-4 rounded hover:bg-blue-700 transition"
        >
          Submit
        </button>

      </form>
    </div>
  );
}
