import React, { useEffect } from 'react';
import { useLocation } from "react-router-dom";

import Interface from '../Components/Interface';
import Navbar from '../Components/Navbar';
import Slider from '../Components/Slider';
import EditionSlider from '../Components/EditionSlider';
import Footer from '../Components/Footer';
import BlogPage from '../Components/BlogPage';

export default function Homepage() {

  const location = useLocation();

  // Scroll to Editions when state is passed
  useEffect(() => {
    if (location.state?.scrollTo === "editions") {
      const target = document.getElementById("editions-section");

      if (target) {
        setTimeout(() => {
          target.scrollIntoView({ behavior: "smooth" });
        }, 300); // small delay to ensure UI is ready
      }
    }
  }, [location]);

  return (
    <>
      <Navbar />
      <Interface />
      <Slider />
      <BlogPage />

      {/*  Add ID here so we can scroll to this section */}
      <div id="editions-section">
        <EditionSlider />
      </div>

      <Footer />
    </>
  );
}
