import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { db } from "../firebase";
import { collection, getDocs, query, orderBy } from "firebase/firestore";

export default function Blogfilter() {
  const [blogs, setBlogs] = useState([]);
  const [filterType, setFilterType] = useState(""); // empty means no filter

  useEffect(() => {
    const fetchBlogs = async () => {
      const q = query(collection(db, "blogs"), orderBy("createdAt", "desc"));
      const snap = await getDocs(q);

      setBlogs(
        snap.docs.map(doc => ({
          docId: doc.id,
          ...doc.data()
        }))
      );
    };
    fetchBlogs();
  }, []);

  // Filtered blogs based on selected type
  const filteredBlogs = filterType
    ? blogs.filter(blog => blog.type === filterType)
    : blogs;

  return (
    <div className="max-w-[1500px] mx-auto p-6">
      {/* Filter Buttons */}
      <div className="flex gap-3 mb-6">
        <button
          className={`px-3 py-1 rounded ${filterType === "" ? "bg-black text-white" : "bg-gray-200"}`}
          onClick={() => setFilterType("")}
        >
          All
        </button>
        <button
          className={`px-3 py-1 rounded ${filterType === "News" ? "bg-black text-white" : "bg-gray-200"}`}
          onClick={() => setFilterType("News")}
        >
          News
        </button>
        <button
          className={`px-3 py-1 rounded ${filterType === "Blog" ? "bg-black text-white" : "bg-gray-200"}`}
          onClick={() => setFilterType("Blog")}
        >
          Blog
        </button>
        {/* Add more types as needed */}
      </div>

      {/* Articles Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-3 lg:grid-cols-4 gap-[40px]">
        {filteredBlogs.map((article, index) => (
          <Link
            key={index}
            to={`/individual/${article.id}`}
            state={{ article }}
            className="group block bg-[#EBEAEA] rounded-2xl shadow-md overflow-hidden relative hover:shadow-xl transition-shadow duration-300"
          >
            <div className="relative p-3 overflow-hidden rounded-2xl">
              <div className="relative bg-white rounded-2xl overflow-hidden">
                <img
                  src={article.image}
                  alt={article.title}
                  className="w-full h-60 object-cover transform transition-transform duration-700 group-hover:scale-105 rounded-2xl"
                />
              </div>

              <span className="absolute top-3 left-3 text-white text-xs px-3 py-1 bg-black/40 rounded">
                {article.type}
              </span>

              <h3 className="absolute top-10 left-6 w-[180px] text-md font-semibold text-white text-left
                  drop-shadow-[2px_2px_4px_rgba(0,0,0,0.8)] rounded">
                {article.title}
              </h3>
            </div>

            <div className="p-4">
              <div className="mt-3 text-sm text-gray-600">
                <p className="font-semibold text-xl text-[#8B0D0D]">
                  {article.author || "Unknown"}
                </p>
                <p>{article.date || ""}</p>
              </div>
            </div>
          </Link>
        ))}
      </div>
    </div>
  );
}
