import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { db } from "../firebase";
import { collection, getDocs, query, orderBy } from "firebase/firestore";

export default function BlogPage() {
  const [blogs, setBlogs] = useState([]);

  useEffect(() => {
    const fetchBlogs = async () => {
      const q = query(collection(db, "blogs"), orderBy("createdAt", "desc"));
      const snap = await getDocs(q);

      setBlogs(
        snap.docs.map(doc => ({
          docId: doc.id,   // Firestore doc ID
          ...doc.data()    // includes your custom ID field
        }))
      );
    };
    fetchBlogs();
  }, []);

  return (
<div className="mt-[12px] grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-10 max-w-[1500px] w-full mx-auto p-6 bg-green-200">
      {blogs.map((article, index) => (
        <Link
          key={index}
          to={`/individual/${article.id}`}   // <-- use your custom ID
          state={{ article }}
          className="group block bg-[#EBEAEA] rounded-2xl shadow-md overflow-hidden relative hover:shadow-xl transition-shadow duration-300"
        >
          <div className="relative p-3 overflow-hidden rounded-2xl">
            <div className="relative bg-white rounded-2xl overflow-hidden">
              <img
                src={article.image}
                alt={article.title}
                className="w-full h-60 object-cover transform transition-transform duration-700 group-hover:scale-105 rounded-2xl"
              />
            </div>

            <span className="absolute top-3 left-3 text-white text-xs px-3 py-1 bg-black/40 rounded">
              { article.type}
            </span>

         <h3 className="absolute top-10 left-6 w-[180px] text-md font-semibold text-white text-left
               drop-shadow-[2px_2px_4px_rgba(0,0,0,0.8)] rounded">
  {article.title}
</h3>

          </div>

          <div className="p-4">
            <div className="mt-3 text-sm text-gray-600">
              <p className="font-semibold text-xl text-[#8B0D0D]">
                {article.author || "Unknown"}
              </p>
              <p>{article.date || ""}</p>
            </div>
          </div>
        </Link>
      ))}
    </div>
  );
}
