import { useEffect, useState } from "react";
import { db } from "../firebase";
import { doc, getDoc } from "firebase/firestore";
import { useParams, useNavigate } from "react-router-dom";

export default function MyIndividual() {
  const { id } = useParams();
  const navigate = useNavigate();
  const [data, setData] = useState(null);

  useEffect(() => {
    const fetchSingle = async () => {
      const docRef = doc(db, "myspace", id);
      const snap = await getDoc(docRef);

      if (snap.exists()) setData(snap.data());
    };

    fetchSingle();
  }, [id]);

  if (!data)
    return (
      <div className="text-white text-center mt-20">Loading...</div>
    );

  return (
    <div className="min-h-screen bg-black text-white p-6">
      <button className="text-xl mb-6" onClick={() => navigate(-1)}>
         Back
      </button>

      <h1 className="text-4xl font-bold">{data.title}</h1>
      <p className="text-lg text-gray-400 mt-2">
        By {data.author}  {data.college}
      </p>

      <div className="mt-6 text-lg whitespace-pre-wrap">
        {data.full}
      </div>
    </div>
  );
}
