import { useState, useEffect, useRef } from "react";
import { Link, useNavigate } from "react-router-dom";
import { X } from "lucide-react";
import { db } from "../firebase";
import { collection, getDocs, query, orderBy } from "firebase/firestore";

export default function SearchResults() {
  const [queryText, setQueryText] = useState("");
  const [blogs, setBlogs] = useState([]);
  const inputRef = useRef(null);
  const navigate = useNavigate();

  // Auto-focus search field
  useEffect(() => {
    inputRef.current?.focus();
  }, []);

  // Fetch blogs from Firestore
  useEffect(() => {
    const fetchBlogs = async () => {
      const q = query(collection(db, "blogs"), orderBy("createdAt", "desc"));
      const snap = await getDocs(q);
      setBlogs(
        snap.docs.map((doc) => ({
          docId: doc.id,
          ...doc.data(),
        }))
      );
    };

    fetchBlogs();
  }, []);

  // Search filtering
  const text = queryText.toLowerCase();
  const filtered = blogs.filter(
    (item) =>
      item.title?.toLowerCase().includes(text) ||
      item.author?.toLowerCase().includes(text) ||
      item.type?.toLowerCase().includes(text)
  );

  return (
    <div className="min-h-screen flex flex-col items-center pt-16 bg-gradient-to-r from-[#9B2A22] to-[#2F0D0A] w-full px-6 pb-10 relative">

      {/* Back Button */}
      <button
        onClick={() => navigate(-1)}
        className="absolute top-6 right-6 text-white hover:text-red-400 transition-colors"
      >
        <X size={30} />
      </button>

      {/* ------------------ Modern Search Bar (Premium UI) ------------------ */}
      <div className="w-full flex justify-center mb-10">
        <div
          className="
            flex items-center gap-3 
            bg-white/90 backdrop-blur-md 
            border border-gray-300 
            rounded-full px-6 py-3
            shadow-lg
            transition-all duration-300 
            hover:shadow-[0_0_25px_rgba(0,0,0,0.15)]
            focus-within:shadow-[0_0_35px_rgba(0,0,0,0.25)]
            hover:scale-[1.02]
          "
          style={{ width: "500px" }}
        >
          <input
            ref={inputRef}
            type="text"
            value={queryText}
            onChange={(e) => setQueryText(e.target.value)}
            placeholder="Search anything..."
            className="
              flex-1 text-gray-800 text-lg 
              bg-transparent outline-none
            "
          />

          {/* Animated Search Icon Button */}
          <button
            className="
              w-10 h-10 flex items-center justify-center 
              rounded-full 
              bg-gray-800 
              hover:bg-black
              transition-all duration-300 
              cursor-pointer 
              active:scale-90
              group
            "
          >
            <svg
              className="w-5 h-5 text-white transition-transform duration-300 group-hover:rotate-12"
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={2}
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="m21 21-4.35-4.35m0 0A7.5 7.5 0 1 0 5.65 5.65a7.5 7.5 0 0 0 10.6 10.6Z"
              />
            </svg>
          </button>
        </div>
      </div>
      {/* ------------------------------------------------------------------ */}

      {/* Title */}
      <div className="w-full max-w-6xl text-left">
        <h1 className="text-4xl sm:text-5xl font-bold text-white mb-10 border-b border-red-500 pb-2">
          
        </h1>
      </div>

      {/* No results */}
      {queryText && filtered.length === 0 && (
        <p className="text-white text-lg mt-10">No results found.</p>
      )}

      {/* Results Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-3 lg:grid-cols-4 gap-[40px] max-w-[1500px] w-full">
        {filtered.map((article) => (
          <Link
            key={article.docId}
            to={`/individual/${article.id}`}
            state={{ article }}
            className="group block bg-[#EBEAEA] rounded-2xl shadow-md overflow-hidden relative hover:shadow-xl transition-shadow duration-300"
          >
            <div className="relative p-3 overflow-hidden rounded-2xl">
              <div className="relative bg-white rounded-2xl overflow-hidden">
                <img
                  src={article.image}
                  alt={article.title}
                  className="w-full h-60 object-cover transform transition-transform duration-700 group-hover:scale-105 rounded-2xl"
                />
              </div>

              <span className="absolute top-3 left-3 text-white text-xs px-3 py-1 bg-black/40 rounded">
                {article.type}
              </span>

              <h3 className="absolute top-10 left-6 w-[180px] text-md font-semibold text-white drop-shadow-[2px_2px_4px_rgba(0,0,0,0.8)]">
                {article.title}
              </h3>
            </div>

            <div className="p-4">
              <div className="mt-3 text-sm text-gray-600">
                <p className="font-semibold text-xl text-[#8B0D0D]">
                  {article.author || "Unknown"}
                </p>
                <p>{article.date || ""}</p>
              </div>
            </div>
          </Link>
        ))}
      </div>
    </div>
  );
}
